import { Component, OnInit } from '@angular/core';
import { MdDialog } from "@angular/material";
import { CoverageDetailComponent } from "app/pages/coverage/coverage-detail/coverage-detail.component";


@Component({
    selector: 'coverage',
    templateUrl: 'coverage.component.html'
})

export class CoverageComponent implements OnInit {

    coberturas = [
        { nombre: 'Muerte accidental', valor: 'Hasta 4 UIT' },
        { nombre: 'Invalidez permanente', valor: 'Hasta 1 UIT' },
        { nombre: 'Incapacidad temporal', valor: 'Hasta 5 UIT' }
    ];

    constructor(public dialog: MdDialog) { }

    ngOnInit() {
    }

    viewPolicies() {
        this.dialog.open(CoverageDetailComponent);
    }
}


