import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { MdDialog } from "@angular/material";
import 'rxjs/add/operator/switchMap';

import { PersonQuery } from "app/pages/person-info/shared/person-query";
import { PoliciesComponent } from "app/pages/person-info/policies/policies.component";

@Component({
    selector: 'person-info',
    templateUrl: 'person-info.component.html'
})

export class PersonInfoComponent implements OnInit {
    placa: string;
    query: PersonQuery;


    departamentos = [
        { value: 'Lima', label: 'Lima' },
        { value: 'Arequipa', label: 'Arequipa' },
        { value: 'La Libertad', label: 'La Libertad' }
    ];

    provincias = [
        { value: 'Lima', label: 'Lima' },
        { value: 'Barranca', label: 'Barranca' },
        { value: 'Cajatambo', label: 'Cajatambo' }
    ];

    distritos = [
        { value: 'Lima', label: 'Lima' },
        { value: 'Miraflores', label: 'Miraflores' },
        { value: 'San Isidro', label: 'San Isidro' }
    ];


    constructor(private route: ActivatedRoute, public dialog: MdDialog) { }

    ngOnInit() {
        this.route.queryParamMap
            .subscribe(param => {
                let tipoDoc = param.get('tipo_documento');
                let doc = param.get('documento');
                this.query = this.getPersonQuery(tipoDoc, doc);
            });
    }

    getPersonQuery(tipoDoc, doc) {
        let personQuery = new PersonQuery();
        personQuery.numeroDocumento = doc;
        personQuery.tipoDocumento = tipoDoc;
        return personQuery;
    }

    viewPolicies() {
        this.dialog.open(PoliciesComponent);
    }
}


