import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FlexLayoutModule } from '@angular/flex-layout';
import { RouterModule, Routes } from '@angular/router';
import { MdIconModule } from "@angular/material";

import { AppComponent } from './app.component';
import { SharedModule } from "app/shared/shared.module";
import { HomeComponent } from "app/pages/home/home.component";
import { PagesModule } from "app/pages/pages.module";
import { MailComponent } from "app/pages/mail/mail.component";
import { PurchaseComponent } from "app/pages/purchase/purchase.component";
import { ConfirmComponent } from "app/pages/confirm/confirm.component";
import { ValidityComponent } from "app/pages/validity/validity.component";
import { CarInfoComponent } from "app/pages/car-info/car-info.component";
import { SearchPersonComponent } from "app/pages/search-person/search-person.component";
import { PersonInfoComponent } from "app/pages/person-info/person-info.component";
import { CoverageComponent } from "app/pages/coverage/coverage.component";

const appRoutes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'vehiculo/:placa', component: CarInfoComponent },
  { path: 'buscar-persona', component: SearchPersonComponent },
  { path: 'persona', component: PersonInfoComponent },
  { path: 'cobertura', component: CoverageComponent },
  { path: 'mail', component: MailComponent },
  { path: 'vigencia', component: ValidityComponent },
  { path: 'pagar', component: PurchaseComponent },
  { path: 'confirm', component: ConfirmComponent },

];
@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SharedModule,
    PagesModule,
    MdIconModule,

    FlexLayoutModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



