import { NgModule } from '@angular/core';
import { BrowserModule } from "@angular/platform-browser";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { MdCardModule, MdButtonModule, MdIconModule, MdInputModule, MdSelectModule, MdDialogModule, MdGridListModule, MdTooltipModule, MdDatepickerModule, MdNativeDateModule, MdCheckboxModule, MdListModule } from "@angular/material";
import { FlexLayoutModule } from "@angular/flex-layout";
import { RouterModule } from "@angular/router";
import { FormsModule } from "@angular/forms";

import { HomeComponent } from "app/pages/home/home.component";
import { MailComponent } from "app/pages/mail/mail.component";
import { PurchaseComponent } from "app/pages/purchase/purchase.component";
import { ConfirmComponent } from "app/pages/confirm/confirm.component";
import { ConfirmDocumentComponent } from "app/pages/confirm/confirm-document.component";
import { ValidityComponent } from "app/pages/validity/validity.component";
import { CarInfoComponent } from "app/pages/car-info/car-info.component";
import { SearchPersonComponent } from "app/pages/search-person/search-person.component";
import { PersonInfoComponent } from "app/pages/person-info/person-info.component";
import { PoliciesComponent } from "app/pages/person-info/policies/policies.component";
import { CoverageComponent } from "app/pages/coverage/coverage.component";
import { CoverageDetailComponent } from "app/pages/coverage/coverage-detail/coverage-detail.component";

@NgModule({
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        FormsModule,
        RouterModule,
        MdCardModule,
        MdButtonModule,
        MdIconModule,
        FlexLayoutModule,
        MdInputModule,
        MdSelectModule,
        MdDialogModule,
        MdGridListModule,
        MdTooltipModule,
        MdNativeDateModule,
        MdDatepickerModule,
        MdCheckboxModule,
        MdListModule
    ],
    exports: [
        HomeComponent,
        CarInfoComponent,
        SearchPersonComponent,
        PersonInfoComponent,
        PoliciesComponent,
        CoverageComponent,
        CoverageDetailComponent,
        MailComponent,
        ValidityComponent,
        PurchaseComponent,
        ConfirmComponent,
        ConfirmDocumentComponent
    ],
    declarations: [
        HomeComponent,
        CarInfoComponent,
        SearchPersonComponent,
        PersonInfoComponent,
        PoliciesComponent,
        CoverageComponent,
        CoverageDetailComponent,
        MailComponent,
        ValidityComponent,
        PurchaseComponent,
        ConfirmComponent,
        ConfirmDocumentComponent
    ],
    entryComponents: [CoverageDetailComponent, ConfirmDocumentComponent, PoliciesComponent],
    providers: [],
})
export class PagesModule { }



